@import "/src/styles/variables";
@import "/src/styles/mixins";

.messageDisplay {
  display: flex;
  flex-flow: column;
  align-items: center;

  &>:first-child {
    margin: .375em;
    font-size: 2.5rem;
  }
}

.warning {
  &>:first-child {
    color: hsl(43, 70%, 55%);
  }
}

.error {
  &>:first-child {
    color: hsl(0, 40%, 50%);
  }
}